@import "var.scss";

.cate_top_photo{
    background: var(--white);
    text-align: center;
    padding: 0 10px;
    margin-top: -30px;
    margin-bottom: 24px;
    .title{
        font: bold 24px/60px var(--font2);
    }
}
.container{
    max-width: 1920px;padding-left: 32px;padding-right: 32px;
}
.list-photo{
    display: flex;flex-wrap: wrap;
    margin: 0 -8px;
    li{margin-bottom: 24px;width: 25%;}
    .item-news{
        border-radius: 4px;overflow: hidden;height: 100%;
        .thumb-art{
            margin-bottom: 0;
        }
        .content{
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
            *{color: #fff;}
        }
        .title-news{
            font: 400 20px/1.3 var(--font1);margin-bottom: 0;
            a{
                padding: 80px 24px 15px 24px;display: block;
                @media (max-width: 1025px){
                    padding: 50px 15px 15px 15px;
                }
                @media (max-width: 860px){
                    padding: 30px 10px 10px 10px;
                }
            }
            @media (max-width: 1200px){
                font-size: 18px;
            }
            @media (max-width: 860px){
                font-size: 16px;
            }
        }
        .status{
            align-items: center;padding:0 24px 24px 24px;
            .share_bookmard{
                margin-left: 40px;
                flex-grow: 3;
                text-align: right;
                a{
                    background: #222222;
                    border-radius: 2px;
                    display: inline-block;width: 30px;height: 30px;line-height: 30px;
                    text-align: center;
                }
                @media (max-width: 860px){
                    margin-left: 10px;
                    a{
                        width: 20px;height: 20px;line-height: 20px;
                    }
                }
            }
            @media (max-width: 1025px){
                padding: 0 15px 15px 15px;
            }
            @media (max-width: 860px){
                padding: 0 10px 10px 10px;
            }
        }
    }
    li{
        padding: 0 8px;
    }
    &_mode1{
        li{
            &:first-child{
                width: 50%;
                .item-news{
                    border-radius: 2px;
                    .title-news{font-size: 28px;
                        @media (max-width: 1200px){
                            font-size: 20px;
                        }
                        @media (max-width: 860px){
                            font-size: 18px;
                        }
                    }
                    .content{
                        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 80%);
                    }
                    .status{
                        .share_bookmard{
                            text-align: left;
                        }
                    }
                }
            }
        }
    }
    &_mode2{
        li{
            &:last-child{
                width: 50%;
                .item-news{
                    border-radius: 2px;
                    .title-news{font-size: 28px;
                        @media (max-width: 1200px){
                            font-size: 20px;
                        }
                        @media (max-width: 860px){
                            font-size: 18px;
                        }
                    }
                    .content{
                        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 80%);
                    }
                    .status{
                        .share_bookmard{
                            text-align: left;
                        }
                    }
                }
            }
        }
    }
}
body.dark_mode{
    .banner-bottom-home{
        background: #222222;
    }
}
